
@import url("https://fonts.googleapis.com/css?family=Cormorant:400,700&display=swap");
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  font-family: 'Cormorant';
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
.index {
  margin: 40px 0;
  margin-top: 20px;
}
h1.indes {
  padding-top: 200px;
}

@import "variables.scss";
@import "mixin.scss";


html {
  scroll-behavior: smooth;
}
body {
  background-color: #fff;
}

input,
textarea {
  background: transparent;
  border: 0;
  border-bottom: 1px solid #231d1f;
  width: 100%;
  line-height: 56px;
  padding-left: 5px;
  margin-bottom: 20px;
  height: 56px;
  &:focus {
    outline: 0;
    border-bottom: 1px solid $colorOnHover;
  }
}
textarea {
  height: 80px;
  line-height: 25px;
}
img {
  width: 100%;
  vertical-align: bottom;
}
.burger {
  position: absolute;
}
a {
  color: inherit;
}
a:hover {
  text-decoration: none;
  color: initial;
}

.butt {
  border: 0;
  outline: 0;
  display: block;
  width: 270px;
  font-family: $secondFont;
  font-size: 20px;
  color: $colorOrangeLight;
  letter-spacing: -0.03px;
  background-image: url(../img/icons/buttBanner.png);
  outline: 1px solid #ff5900;
  text-transform: uppercase;
  text-align: center;
  line-height: 50px;
  height: 50px;
  transition: all 0.3s ease;
  font-weight: 700;
  &:hover {
    color: $colorOrangeLight;
    outline: 0;
    text-decoration: none;
    span {
      background-position: 100% 1px;
    }
  }
  span {
    padding-right: 25px;
    background-image: url(../img/icons/buttArrow.svg);
    background-repeat: no-repeat;
    background-position: 98% 1px;
  }
}
h3 {
  font-size: 54px;
  text-transform: uppercase;
  text-align: center;
  color: white;
  line-height: 100px;
  font-family: $secondFont;
}
.crumbs {
  display: flex;
  margin-bottom: 50px;
  li {
    padding: 0 15px;
    padding-right: 20px;
    font-size: 18px;
    color: #5e5e61;
    text-transform: uppercase;
    letter-spacing: -0.03px;
    background-image: url(../img/icons/arrowCrumbs.svg);
    background-repeat: no-repeat;
    background-position: 100% 3px;
    &:last-child {
      background-image: none;
    }
  }

}

.slick-slide {
  &:focus {
    outline: 0;
  }
}

//request

.request {
  background-image: url(../img/icons/bcgRequest.png);
  background-color: #eee9e6;
  background-size: cover;
  background-position: 0 100%;
  padding: 70px 0;
  .title {
    font-size: 60px;
    font-family: $secondFont;
    color: black;
    font-weight: 700;
  }
  p {
    margin-top: 40px;
    font-size: 18px;
  }
  form {
    padding: 70px;
    margin-left: -150px;
    padding-left: 200px;
    background: #d7d2d0;
    box-shadow: 0 6px 20px 0 rgba(110, 110, 110, 0.5);
  }
  .blocOne {
    position: relative;
    z-index: 2;
    margin-right: -60px;
  }
  .blocTwo {
    position: relative;
    z-index: 1;
  }
  .butt {
    margin-right: 0;
    margin-left: auto;
    margin-top: 20px;
  }
  input {
    margin-bottom: 0;
  }
  ::placeholder {
    line-height: 80px;
  }
}

// one product

.oneProduct {
  margin-bottom: 30px;
  position: relative;
  .itemDescr {
    display: flex;
  }
 .itemDescr .name {
    width: 100%;
    background-color: #fff;
    padding: 10px;
    float: none;
    div:first-child {
      font-size: 20px;
      color: #353535;
      letter-spacing: -0.03px;
      line-height: 30px;
      text-transform: uppercase;
    }
  }
  .price {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: $priceBcg;
    width: 40%;
    color: $colorOrangeLight;
    .priceReduce{
      text-decoration: line-through;
    }
  }
  .slideItem {
    margin-bottom: 30px;
    padding-left: 10px;
    padding-right: 10px;
  }
  .butt {
    margin-right: auto;
    margin-left: auto;
    span {
      background-image: url(../img/icons/arrowOrange.svg);
      background-size: 20px;
      background-position: 98% 2px;
      padding-right: 35px;
    }
  }
  .salesReduce {
    position: absolute;
    top: -10px;
    left: 20px;
    background-color: $colorOrangeLight;
    line-height: 24px;
    transform: rotate(90deg);
    padding: 7px;
    font-size: 24px;
    color: #ffffff;
    letter-spacing: -0.03px;
  }
}

//burger

#burger {
  position: absolute;
  top: 28px;
  left: 15px;
  z-index: 11;
  cursor: pointer;
  height: 30px;
  width: 30px;
  text-align: center;
  float: right;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  display: none;
  &:after,
  &:before {
    content: "";
    height: 2px;
    width: 30px;
    border-radius: 0px;
    display: inline-block;
    position: absolute;
    left: 0;
    background-color: #ffff;
    transition: all 0.2s ease-out;
  }
  &:after {
    bottom: 6px;
  }
  &:before {
    top: 6px;
  }
  .iconBar {
    width: 30px;
    height: 2px;
    display: inline-block;
    margin: 14px 0;
    background-color: #ffff;
    transition: all 0.2s ease-out;
    border-radius: 3px;
  }
}
#burger.burgerOpen {
  .iconBar {
    width: 0;
  }
  &:after {
    transform: rotate(-45deg);
    bottom: 14px;
  }
  &:before {
    transform: rotate(45deg);
    top: 14px;
  }
}

// bannerItem

.slideItem{
  position: relative;
  .slideInner {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
  }
  .contentInner {
    width: 52%;
    margin-left: -30px;
    margin-top: 150px;
  }
  .miniBanner {
    position: relative;
    margin-bottom: 50px;
    img {
      box-shadow: 0 6px 32px 0 rgba(101, 100, 100, 0.74);
    }
  }
  .name {
    font-size: 54px;
    margin-bottom: 30px;
    padding-right: 30px;
  }
  p {
    font-size: 24px;
    color: #ffffff;
    letter-spacing: -0.04px;
    padding-right: 60px;
  }
  .text {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 60px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100%;
  }
}

@media only screen and (max-width: 768px) {
  .butt {
    max-width: 335px;
  }
  h3 {
    font-size: 30px;
    line-height: 35px;
    margin-top: 20px;
    margin-bottom: 20px;
  }
  .butt {
    margin-left: auto;
    margin-right: auto;
  }
  .request {
    form {
      margin-left: 0;
      padding: 20px;
      margin-top: 30px;
    }
    .butt {
      margin-right: auto;
    }
    .title{
      font-size: 45px;
      font-weight: 400;
    }
    p{
      margin-top: 20px;
    }
  }
  .crumbs {
    display: none;
  }
  .slideItem{
    .contentInner{
      margin-top: 0;
      width: 100%;
      margin-left: 0;
    }
    .miniBanner {
      margin-bottom: 0;
      height: 250px;
    }
    .name {
      font-size: 30px;
      margin-bottom: 15px;
      margin-top: 15px;
    }
    .text {
      left: 15px;
      justify-content: flex-start;
    }
    img {
      height: 100%;
      object-fit: cover;
      object-position: 100%;
    }
    .slideInner{
      height: 250px;
      bottom: 0;
      top: auto;
    }
  }
}

// Header

header {
  height: 125px;
  background-color: #1F0D03;
  position: relative;
  a.logoMobile {
    display: none;
  }
  .relativeHeader {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 10;
    width: 100%;
    background-color: #1F0D03;
  }
  .headerBlock {
    height: 125px;
    margin-left: auto;
    margin-right: auto;
    background-color: #1F0D03;
    position: relative;
  }
  .menuUp {
    justify-content: space-between;
  }
  .menuUpLeft,
  .menuCabinet {
    margin-top: 25px;
    display: flex;
    justify-content: space-between;
    margin-bottom: 0;
  }
  .menuUpLeft a,
  .menuCabinet .buttMenu span {
    padding: 0 15px;
    display: block;
    line-height: 24px;
    color: #fff;
    font-size: 18px;
    font-family: $secondFont;
    cursor: pointer;
    &:hover {
      text-decoration: none;
      color: #ffff;
    }
  }
  .menuCabinet .buttMenu span {
    padding-left: 30px;
  }
  .menuCabinet .buttMenu:last-child span{
    padding-right: 0;
  }
  a.logo {
    margin-left: auto;
    margin-right: auto;
    position: absolute;
    left: 0;
    right: 0;
    width: 200px;
    top: 10px;
  }
  .menuCabinet .buttMenu {
    padding-right: 0;
    margin-left: 15px;
    margin-right: 15px;
    height: 20px;
    display: block;
    background-repeat: no-repeat;
    background-position: 0 center;
    span:hover {
      color: #fff;
    }
  }
  .buttMenu.buttLang {
    padding: 0;
    padding-right: 20px;
    span {
      padding: 0;
    }
  }
  .buttMenu.buttSearch {
    background-image: url(../img/icons/searchOrange.svg);
    background-size: 20px;
    background-position: 0 0;
    width: 30px;
    span{
      width: 100%;
      height: 100%;
      padding: 0;
    }
  }
  .menuCabinet .buttMenu:last-child{
    padding-right: 10px;
  }
  .menuCenter {
    margin-top: 25px;
    margin-bottom: 0;
    justify-content: space-between;
    ul {
      display: flex;
    }
    li,
    a {
      line-height: 50px;
      text-transform: uppercase;
      padding: 0 8px;
      color: #ffff;
      font-family: $secondFont;
      font-weight: 700;
      cursor: pointer;
      &:hover {
        text-decoration: none;
        color: #ffff;
      }
      span {
        font-weight: 700;
        font-family: $secondFont;
      }
    }
    li {
      padding-right: 20px;
      margin-right: 20px;
      font-family: $secondFont;
      font-weight: 700;
    }
    .menuItem {
      position: relative;
      background-image: url(../img/icons/arrowBordo.svg);
      background-repeat: no-repeat;
      background-position: 100% center;
      background-size: 16px;
      .submenuItem span{
        font-weight: normal;
      }
      span {
        color: #ffff;
        font-family: $secondFont;
        font-weight: 700;
      }
      &:hover > span {
        color: #ffff;
      }
      &:hover > .submenu {
        display: block;
      }
    }
    .submenu {
      display: none;
      position: absolute;
      left: -15px;
      background-color: $bcgHome;
      min-width: 250px;
      padding-bottom: 20px;
      padding-top: 20px;
      box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.5);
      span{
        text-transform: initial;
      }
      li {
        display: block;
        padding: 0;
        margin: 0;
        font-size: 20px;
        &:hover {
          background-image: url(../img/icons/bcgHover.png);
          background-size: 100% 100%;
          a {
            color: white;
          }
        }
      }
      a {
        font-size: 20px;
        color: #ffff;
        letter-spacing: -0.04px;
        line-height: 50px;
        white-space: nowrap;
        padding-left: 25px;
        margin: 0;
        display: block;
        text-transform: initial;
        text-align: left;
        font-weight: 400;
        &:hover {
          text-decoration: none;
          color: black;
        }
      }
    }
    li.submenuItem {
      padding-left: 25px;
      &:hover > .submenuItemBloc {
        display: flex;
      }
      .submenuItemBloc {
        background-color: #fff;
        top: 0;
        bottom: 0;
        position: absolute;
        display: none;
        flex-direction: column;
        min-width: 100%;
        right: -100%;
        z-index: 10;
        height: 100%;
        li:hover{
          background-image: none;
        }
        a {
          font-family: $secondFont;
          font-size: 20px;
          color: #531F03;
          letter-spacing: -0.04px;
          text-align: left;
          line-height: 47px;
          &:hover {
            font-weight: bold;
          }
        }
      }
    }
  }
  .menuCenter > ul:last-child {
    li{
      &:last-child{
        display: none;
      }
      padding-right: 0;
      margin-right: 15px;
    }
  }
  .menuCenter .menuItem > span{
    padding-right: 10px;
  }
}
