footer {
  background-image: $footerBcg;
  background-size: cover;
  background-position: 100%;
  background-attachment: fixed;
  padding-top: 80px;
  position: relative;
  z-index: 2;
  .footerBloc li > p {
    color: white;
    white-space: nowrap;
    margin: 0;
  }
  a.logo{
    width: 60%;
    display: block;
  }
  .footerBloc {
    margin-top: 70px;
    &:first-child{
      margin-top: 0;
    }
    .line {
      width: 60px;
      height: 4px;
      background-image: url(../img/icons/lineFooter.svg);
      background-size: 100%;
      background-repeat: no-repeat;
    }
    p {
      text-align: left;
      margin-top: 15px;
      padding-right: 80px;
      font-size: 18px;
      line-height: 25px;
    }
    &:first-child {
      .footerTitle {
        font-family: $secondFont;
        font-size: 55px;
        color: $colorOrangeLight;
        text-transform: none;
        display: block;
        div:last-child {
          font-size: 25px;
        }
      }
    }
  }
  ul {
    margin-bottom: 0;
    margin-top: 15px;
  }
  a,
  .footerTitle {
    font-family: $footerFont;
    font-size: 18px;
    color: #ffffff;
    letter-spacing: 0;
    line-height: 30px;
    margin-bottom: 10px;
  }
  a:hover {
    color: $colorOrangeLight;
    text-decoration: none;
  }
