.containerRadio {
    padding-left: 35px;
    display: block;
    position: relative;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    text-align: left;
    margin: 10px 0;
    font-size: 18px;
    line-height: 26px;
}

.containerRadio input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    left: 0;
}

.containerRadio .checkmark {
    position: absolute;
    top: 0;
    left: 0;
    height: 26px;
    width: 26px;
    outline: 1px solid #1F0D03
}

.containerRadio:hover input~.checkmark {
    border: 1px solid silver;
}

.containerRadio input:checked~.checkmark {
    display: block;
}

.containerRadio .checkmark:after {
    content: "";
    position: absolute;
    display: none
}

.containerRadio input:checked~.checkmark:after,
.containerRadio input:checked ~ .checkmark:before {
    display: block
}
.containerRadio .checkmark:before {
  content: '';
  position: absolute;
  width: 20px;
  height: 20px;
  border: 1px solid #E14E00;
  top: 3px;
  left: 3px;
  z-index: 1;
  background: #1f0d03;
  display: none;
}
.containerRadio .checkmark:after {
    left: 11px;
    top: 7px;
    width: 5px;
    height: 10px;
    border: solid #fff;
    border-width: 0 3px 3px 0;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
    z-index: 2;
}
}
